*, ::before, ::after{
  --tw-shadow: 0 0 #0000;
}

.shadow-card-initial{
  --tw-shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.2) ;
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.shadow-list-section{
  --tw-shadow:  0px 2px 10px rgba(0, 0, 0, 0.1);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.hover\:shadow-card-final:hover{
  --tw-shadow: 0px 10px 36px 0px rgba(0, 0, 0, 0.16);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.focus\:outline-none:focus{
  outline: 2px solid transparent;
  outline-offset: 2px;
}

*, ::before, ::after{
  --tw-ring-inset: var(--tw-empty,/*!*/ /*!*/);
  --tw-ring-offset-width: 0px;
  --tw-ring-offset-color: #fff;
  --tw-ring-color: rgba(59, 130, 246, 0.5);
  --tw-ring-offset-shadow: 0 0 #0000;
  --tw-ring-shadow: 0 0 #0000;
}

.transition-all{
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition{
  transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-backdrop-filter;
  transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition-colors{
  transition-property: background-color, border-color, color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition-transform{
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.duration-200{
  transition-duration: 200ms;
}

.duration-300{
  transition-duration: 300ms;
}

.duration-500{
  transition-duration: 500ms;
}

.ease-in-out{
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.header {
  min-height: 120vh;
  max-height: 130vh;
  background-color: rgb(187, 13, 50);
  background-opacity: 88%;
}

.header-large {
  font-size: clamp(36px, 10vw, 60px);
}

.header-small {
  font-size: 18px;
}

.section-title-font-size {
  font-size: clamp(32px, 10vw, 36px);
}

.features {
  min-height: 100vh;
}

.feature-title-small,
.time-header-detail,
.card-section-small {
  font-size: clamp(16px, 10vw, 20px);
}

.feature-detail,
.subscribe-small,
.screenshot-small,
.section-detail-small {
  font-size: clamp(14px, 10vw, 18px);
}

.quotes {
  font-size: clamp(20px, 10vw, 28px);
}

.quotes {
  quotes: "\201C""\201D""\2018""\2019";
}

.quotes:before {
  color: #ff3d65;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  vertical-align: -0.4em;
}

.quotes:after {
  color: #ff3d65;
  content: close-quote;
  font-size: 4em;
  line-height: 0.1em;
  vertical-align: -0.65em;
}

.background {
  z-index: -10;
}

.accordion-content {
  padding-top: 1.25rem;
  overflow: hidden;
  max-height: 1000px;
  transition: max-height 1s ease-in-out, padding 0.2s;
}

.accordion-content[aria-expanded="true"] {
  padding: 0rem;
  max-height: 0px;
  transition: max-height 0.5s cubic-bezier(0, 1, 0, 1), padding 0.2s;
}

.slick-dots li.slick-active button:before {
  color: red;
}

@media (min-width: 640px){
  .sm\:col-span-2{
    grid-column: span 2 / span 2;
  }

  .sm\:max-w-xl{
    max-width: 36rem;
  }

  .sm\:grid-cols-2{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .sm\:flex-row{
    flex-direction: row;
  }

  .sm\:space-x-5 > :not([hidden]) ~ :not([hidden]){
    --tw-space-x-reverse: 0;
    margin-right: calc(1.25rem * var(--tw-space-x-reverse));
    margin-left: calc(1.25rem * calc(1 - var(--tw-space-x-reverse)));
  }

  .sm\:space-y-0 > :not([hidden]) ~ :not([hidden]){
    --tw-space-y-reverse: 0;
    margin-top: calc(0px * calc(1 - var(--tw-space-y-reverse)));
    margin-bottom: calc(0px * var(--tw-space-y-reverse));
  }
}

